#toast-container {
  position: fixed;
  z-index: 999;
  top: 24px;
  bottom: auto;
  right: 24px;
  left: auto;
  min-width: 200px; }
  #toast-container #notification {
    padding: 16px;
    margin-bottom: 8px;
    border-radius: 6px;
    display: flex;
    align-items: center;
    background: #e5e5e5;
    color: #fff; }
    #toast-container #notification.success {
      background-color: #68B8C8; }
    #toast-container #notification.info {
      background-color: #7AC29A; }
    #toast-container #notification.warn {
      background-color: #F3BB45; }
    #toast-container #notification.danger {
      background-color: #CC2127; }
